import React, { Component } from 'react';
import { Button, Checkbox, Container, Divider, FormControl, FormControlLabel, Grid, IconButton, InputAdornment, InputLabel, Link, Paper, Select, TextField, Typography } from '@material-ui/core'
import { AccountBox, Email, Lock, School, Visibility, VisibilityOff, CheckCircle, LocationCity, Fingerprint } from '@material-ui/icons'
import { initializeApp } from "firebase/app";
import { getDatabase, ref, onValue, get, off, set} from "firebase/database"

const liff = window.liff;

export default class Register extends Component {

    constructor(props) {
        super(props);

        this.state = {
            userLineID: "",
            pictureUrl: "",
            name: "",
            email: "",
            code: "",
            company: "Innova Interactive Studio",
            type: 0,
            password: "",
            status: {
                complete: false,
                existed: false,
            }
        };
    }

    componentDidMount = async () => {
        await liff.init({ liffId: "1657793014-QWEljPNV" }).catch(err => { throw err });
        if (liff.isLoggedIn()) 
        {
            let getProfile = await liff.getProfile();
            this.setState(
            {
                userLineID: getProfile.userId,
                pictureUrl: getProfile.pictureUrl,
            }, this.InitializeFirebase);
        } 
        else 
        {
            liff.login();
        }
    }


    InitializeFirebase = () => 
    {
        const firebaseConfig = 
        {
            apiKey           : "AIzaSyBsAZ-KDml8bLZP-_KPSwsxeJW6ZXlTPEw",
            authDomain       : "work-checker-innova-99364.firebaseapp.com",
            databaseURL      : "https://work-checker-innova-99364-default-rtdb.asia-southeast1.firebasedatabase.app",
            projectId        : "work-checker-innova-99364",
            storageBucket    : "work-checker-innova-99364.appspot.com",
            messagingSenderId: "890549897742",
            appId            : "1:890549897742:web:3ad79956d4046ac64e8a5f"
        };

        const app = initializeApp(firebaseConfig);
        const db = getDatabase();
        const data = ref(db, this.state.userLineID);
        console.log(this.state.userLineID);
        get(data).then((snapshot) => 
        {
            if (snapshot.val() == null)
            {
                this.setState(
                {
                    status: 
                    {
                        complete: true,
                        existed: false
                    }
                })               
            }
            else
            {
                let value = snapshot.val();
                this.setState(
                {
                    name: value.name,
                    email: value.email,
                    code: value.code,
                    type: value.type,
                    company: value.company,
                    status: 
                    {
                        complete: true,
                        existed: true
                    }
                })               
            }
        });
    }


    OnRegister = () => 
    {
        if (this.state.name === "")
        {
            alert("Name is empty");
            return;
        }

        if (this.state.email === "")
        {
            alert("Email is empty");
            return;
        }

        if (this.state.code === "")
        {
            alert("Code is empty");
            return;
        }

        if (this.state.password.toLowerCase() !== "runeluna")
        {
            alert("Password isn't correct");
            return;
        }

        const db = getDatabase();
        const data = ref(db, this.state.userLineID);
        
        let json = {
            name: this.state.name,
            email: this.state.email,
            code: this.state.code,
            type: this.state.type,
            company: this.state.company
        };
        
        let status = this.state.status;
        status.complete = false;
        this.setState({
            status: status
        }, () => 
        {
            set(data, json).then(() => 
            {
                setInterval(() =>liff.closeWindow(), 1500 );
                
            });
        })

    }


    RenderForm = () => {
        return (
            <Grid container spacing={2} direction="column" justifyContent="center" alignItems="center">

                {/* Logo */}
                <Grid item>
                    <img src={this.state.pictureUrl} alt="logo" width={128}/>
                </Grid>

                {/* Username */}
                <Grid item container spacing={1} direction="row">
                    <Grid item xs={12}>
                        <TextField
                            label="Name"
                            value={this.state.name}
                            onChange={(e) => this.setState({ name: e.target.value })}
                            InputProps={{
                                startAdornment: <InputAdornment position="start"><AccountBox /></InputAdornment>,
                            }}
                            variant="outlined"
                        />
                    </Grid>
                </Grid>

                <Grid item xs={12} style={{ width: "100%" }}>
                    <TextField
                        label="Email"
                        fullWidth={true}
                        value={this.state.email}
                        onChange={(e) => this.setState({ email: e.target.value })}
                        InputProps={{
                            startAdornment: <InputAdornment position="start"><Email /></InputAdornment>,
                        }}
                        variant="outlined"
                    />
                </Grid>

                <Grid item xs={12} style={{ width: "100%" }}>
                    <TextField
                        label="Code"
                        type="text"
                        fullWidth={true}
                        value={this.state.code}
                        onChange={(e) => {
                            this.setState({ code: e.target.value })
                        }}
                        InputProps={{
                            startAdornment: <InputAdornment position="start"><Fingerprint /></InputAdornment>,
                        }}
                        variant="outlined"
                    />
                </Grid>


                <Grid item xs={12} style={{ width: "100%" }}>
                    <FormControl variant="outlined">
                        <Select
                            native
                            value={this.state.company}
                            onChange={(e) => this.setState({ company: e.target.value })}
                            inputProps={{
                                name: 'Company',
                            }}
                        >
                            <option value={"Innova Interactive Studio"}>Innova Interactive Studio</option>
                            <option value={"Fourteen Rain Studio"}>Fourteen Rain Studio</option>
                        </Select>
                    </FormControl>
                </Grid>


                <Grid item xs={12} style={{ width: "100%" }}>
                    <FormControl variant="outlined">
                        <Select
                            native
                            value={this.state.type}
                            onChange={(e) => this.setState({ type: e.target.value })}
                            inputProps={{
                                name: 'Type',
                            }}
                        >
                            <option value={0}></option>
                            <option value={1}>Free Lance</option>
                        </Select>
                    </FormControl>
                </Grid>


                <Grid item xs={12} style={{ width: "100%" }}>
                    <TextField
                        label="Secret Password"
                        type="text"
                        fullWidth={true}
                        value={this.state.password}
                        onChange={(e) => {
                            this.setState({ password: e.target.value })
                        }}
                        InputProps={{
                            startAdornment: <InputAdornment position="start"><Lock /></InputAdornment>,
                        }}
                        variant="outlined"
                    />
                </Grid>

                {/* Button */}
                <Grid item container spacing={1} direction="row">
                    <Grid item xs={12} style={{ width: "100%" }}>
                        <Button variant="contained" color="primary" onClick={this.OnRegister.bind(this)} fullWidth={true}>
                            {this.state.status.existed ? "Update" : "Register"}
                        </Button>
                    </Grid>
                </Grid>
            </Grid>
        )
    }


    render() 
    {
        let centerV = {
            position: "absolute",
            top: "50%",
            left: "50%",
            transform: "translate(-50%, -50%)"
        }

        return (
            <Container style={centerV}> 
                <Grid container direction="row" justifyContent="center" alignItems="center">
                    <Paper style={{padding: 24}} elevation={3}>
                        <Grid item>
                            {
                                this.state.status.complete && (
                                    this.RenderForm()
                                )
                            }
                            {
                                !this.state.status.complete && (
                                    "Loading..."
                                )
                            }
                        </Grid>
                    </Paper>
                </Grid>
            </Container>
        )

    }
}