import React, { Component } from 'react';
import { Button, Checkbox, Container, Divider, FormControl, FormControlLabel, Grid, IconButton, InputAdornment, InputLabel, Link, Paper, Select, TextField, Typography } from '@material-ui/core'
import { AccountBox, Email, Lock, School, Visibility, VisibilityOff, CheckCircle, LocationCity, Fingerprint } from '@material-ui/icons'
import { initializeApp } from "firebase/app";
import { getDatabase, ref, onValue, get, off, set} from "firebase/database"
import iAPI from '../utils/iAPI';

const liff = window.liff;

export default class CheckIn extends Component {

    constructor(props) {
        super(props);

        this.state = {
            userLineID: "",
            pictureUrl: "",
            name: "",
            email: "",
            code: "",
            company: "",
            type: 0,
            update: "",
            status: {
                complete: false,
                existed: false,
                message: "Loading"
            }
        };
    }

    componentDidMount = async () => {
        await liff.init({ liffId: "1657793014-o24L1gk9" }).catch(err => { throw err });
        if (liff.isLoggedIn()) 
        {
            let getProfile = await liff.getProfile();
            this.setState(
            {
                userLineID: getProfile.userId,
                pictureUrl: getProfile.pictureUrl,
            }, this.InitializeFirebase);
        } 
        else 
        {
            liff.login();
        }
    }


    InitializeFirebase = () => 
    {
        const firebaseConfig = 
        {
            apiKey           : "AIzaSyBsAZ-KDml8bLZP-_KPSwsxeJW6ZXlTPEw",
            authDomain       : "work-checker-innova-99364.firebaseapp.com",
            databaseURL      : "https://work-checker-innova-99364-default-rtdb.asia-southeast1.firebasedatabase.app",
            projectId        : "work-checker-innova-99364",
            storageBucket    : "work-checker-innova-99364.appspot.com",
            messagingSenderId: "890549897742",
            appId            : "1:890549897742:web:3ad79956d4046ac64e8a5f"
        };

        const app = initializeApp(firebaseConfig);
        const db = getDatabase();
        const data = ref(db, this.state.userLineID);
        console.log(this.state.userLineID);
        get(data).then((snapshot) => 
        {
            if (snapshot.val() == null)
            {
                this.setState(
                {
                    status: 
                    {
                        complete: false,
                        existed: false,
                        message: ""
                    }
                })               
            }
            else
            {
                let value = snapshot.val();
                this.setState(
                {
                    name: value.name,
                    email: value.email,
                    code: value.code,
                    type: value.type,
                    company: value.company,
                    status: 
                    {
                        complete: true,
                        existed: true
                    }
                })               
            }
        });
    }


    OnCheckIn = () => 
    {
        if (this.state.name === "")
        {
            alert("Name is empty");
            return;
        }

        if (this.state.email === "")
        {
            alert("Email is empty");
            return;
        }

        if (this.state.code === "")
        {
            alert("Code is empty");
            return;
        }

        let form = new URLSearchParams();
        form.append("entry.1940245042", this.state.email);
        form.append("entry.1642076706", this.state.company);
        form.append("entry.1176442603", this.state.name);
        form.append("entry.449446392", "");
        form.append("entry.2090110175", this.state.type == 0 ? "" : "FreeLance");
        form.append("entry.665609104", this.state.update);
        form.append("pageHistory", "0,1,2");
        form.append("submit", "Submit");
        
        let status = this.state.status;
        status.complete = false;
        status.message = "Checking... In";
        let self = this;
        this.setState({status: status}, () => 
        {
            iAPI.GET("https://docs.google.com/forms/d/e/1FAIpQLScptseT4XN75pFpIW11tWrhUZ3ACq47eKQOacXPHIj7B2-03w/formResponse", form, (res) => 
            {
                status.message = "Complete...";
                self.setState({status: status}, () => 
                {
                    setTimeout(() => liff.closeWindow(), 1500);
                })
            });
        })
        
    }


    RenderForm = () => {
        return (
            <Grid container spacing={2} direction="column" justifyContent="center" alignItems="center">

                {/* Logo */}
                <Grid item>
                    <img src={this.state.pictureUrl} alt="logo" width={64}/>
                </Grid>

                {/* Username */}
                <Grid item container spacing={1} direction="row">
                    <Grid item xs={12}>
                        <TextField
                            label="Update"
                            type="textarea"
                            multiline={true}
                            minRows={3}
                            placeholder="What did you do?"
                            value={this.state.update}
                            onChange={(e) => this.setState({ update: e.target.value })}
                            variant="outlined"
                        />
                    </Grid>
                </Grid>


                {/* Button */}
                <Grid item container spacing={1} direction="row">
                    <Grid item xs={12} style={{ width: "100%" }}>
                        <Button variant="contained" color="primary" onClick={this.OnCheckIn.bind(this)} fullWidth={true}>
                            Check In
                        </Button>
                    </Grid>
                </Grid>
            </Grid>
        )
    }


    render() 
    {
        let centerV = {
            position: "absolute",
            top: "50%",
            left: "50%",
            transform: "translate(-50%, -50%)"
        }

        return (
            <Container style={centerV}> 
                <Grid container direction="row" justifyContent="center" alignItems="center">
                    <Paper style={{padding: 24}} elevation={3}>
                        <Grid item>
                            {
                                this.state.status.complete && (
                                    this.RenderForm()
                                )
                            }
                            {
                                !this.state.status.complete && (
                                    this.state.status.message
                                )
                            }
                        </Grid>
                    </Paper>
                </Grid>
            </Container>
        )

    }
}