var host = "http://27.254.191.118:2280";
//var host = "http://171.103.163.114:2280";
//var host = "http://123.242.164.7:8081";
class iAPI 
{
    static IsServerTest()
    {
        return host === "http://171.103.163.114:1150";
    }


    static POST(url, formData, response, error)
    {

        var formBody = [];
        for (var property in formData) {
            var encodedKey = encodeURIComponent(property);
            var encodedValue = encodeURIComponent(formData[property]);
            formBody.push(encodedKey + "=" + encodedValue);
        }
        formBody = formBody.join("&");

        fetch(url, {
            method: "post",
            body: formBody,
            mode: "no-cors",
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'
            }
        })
        .then(res => response(res))
        .catch(msg => 
        {
            alert("POST: " + url + " # " + msg);
        });
    };

    static GET(url, formData, response, error)
    {
        console.log(url + "?" + formData);
        fetch(url + "?" + formData, {
            method: "get",
            mode: "no-cors",
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'
            }
        })
        .then(res => response(res))
        .catch(msg => 
        {
            alert("POST: " + url + " # " + msg);
        });
    };
}

export default iAPI